import React from "react";
import { motion } from "framer-motion";
import Header from "../components/Header";
import SkillCard from "../components/SkillCard";
import "../styles/Portfolio.css";
import htmlLogo from "../assets/HTMLLOGO.png";
import cssLogo from "../assets/CSSLOGO.png";
import reactLogo from "../assets/REACTLOGO.png";

function Portfolio({ isDarkMode, toggleDarkMode }) {
  return (
    <motion.div
      className={`portfolio ${isDarkMode ? "dark-mode" : "light-mode"}`}
      initial={{ y: "100%" }}
      animate={{ y: 0 }}
      transition={{ duration: 0.5, ease: "easeInOut" }}
    >
      <Header isDarkMode={isDarkMode} toggleDarkMode={toggleDarkMode} />
      <main className="content">
        <section className="intro-section">
          <h2>Bienvenue sur mon portfolio !</h2>
          <p>Jeune dveloppeur web en fin de formation OpenClassrooms, j'ai dcid de me lancer dans l'auto-entreprenariat par motivation de la passion pour la cration.</p>
        </section>
        <section className="project-section bg-color-1">
          <h2>Projet 1 : Crer une application web de location immobilire avec React</h2>
          <p>
            Ce projet consistait  dvelopper une application Web moderne pour
            un site de location immobilire en utilisant React. L'application
            permet aux utilisateurs de parcourir des annonces immobilires,
            d'afficher les dtails des proprits et d'explorer une galerie
            d'images pour chaque annonce. C'taient mes premiers pas avec React
            et a m'a confort dans mon choix de m'orienter vers le front-end
            plus que le back-end pour l'aspect plus cratif du monde du devweb.
          </p>
          <p>
            Problmatique rencontre :
            La gestion du routage et de l'tat dans une application React a t un dfi au dbut, mais j'ai surmont cela en utilisant React Router pour organiser la navigation et en apprenant  grer efficacement l'tat avec useState et useEffect.
          </p>
          <p>
            Tout au long de ce projet, j'ai donc appris React, l'utilisation de
            bibliothques telles que React Router pour mettre en place le
            routing. J'ai aussi utilis des props et gr le state, notamment
            pour l'animation des collapse.
          </p>
          <p>
            Pour faire simple, je suis vite devenu passionn de React et j'ai
            hte de prendre de l'exprience pour apporter des touches uniques
            dans chaque travaux en utilisant React  son plein potentiel.
          </p>
          <h3>Comptences dveloppes :</h3>
          <ul>
            <li>Matrise de React pour le dveloppement front-end.</li>
            <li>Mise en uvre du routage avec React Router.</li>
            <li>
              Gestion de l'tat et des props pour des composants rutilisables.
            </li>
            <li>
              Application des principes d'UX/UI pour une exprience utilisateur
              fluide.
            </li>
          </ul>
          <button 
            className="project-link-button"
            onClick={() => window.open('https://ugocapelle.github.io/P5_React_OC/', '_blank', 'noopener,noreferrer')}
          >
            Lien vers le Github Page
          </button>
        </section>
        <section className="project-section bg-color-2">
          <h2>Projet 2 : Dbugger et optimiser un site de photographe</h2>
          <p>
            Dans ce projet, mon objectif principal tait de dbuguer et d'optimiser un site de photographe pour amliorer  la fois ses performances, son exprience utilisateur ainsi que de corriger le CEO. Le site prsentait plusieurs problmes de performance, notamment un temps de chargement lev d  la mauvaise gestion des images et des fichiers lourds. J'ai utilis des outils d'Audit comme LightHouse pour identifier les problmes et effectuer des corrections comme la compression des images et le lazy loading.
          </p>
          <p>
            L'un des plus grands dfis a t de rduire le temps de chargement du site, en particulier en ce qui concerne la gestion des images. Aprs avoir identifi que les images taient mal optimises, j'ai mis en uvre des solutions comme la compression d'images et l'ajout du lazy loading pour que les images ne se chargent que lorsqu'elles sont visibles  l'cran.
          </p>
          <p>
            J'ai galement travaill sur le rfrencement naturel (SEO) du site pour le rendre plus visible sur les moteurs de recherche et ainsi attirer plus de trafic. Cela a impliqu des ajustements techniques comme l'optimisation des balises meta, la correction des liens briss et la rcriture de certaines URL.
          </p>
          <h3>Comptences dveloppes :</h3>
          <ul>
            <li>Optimisation des performances d'un site web, notamment par la compression d'images et l'implmentation du lazy loading.</li>
            <li>Utilisation d'outils d'audit comme LightHouse pour identifier les problmes de performance et de SEO.</li>
            <li>Amlioration du rfrencement naturel (SEO) en optimisant les balises meta et la structure des URL.</li>
            <li>Correction des problmes d'accessibilit pour garantir une meilleure exprience utilisateur.</li>
            <li>Gestion des fichiers lourds et mise en uvre de bonnes pratiques pour une gestion efficace des ressources.</li>
          </ul>
          <button 
            className="project-link-button"
            onClick={() => window.open('https://ugocapelle.github.io/P4.OC.Nina.Carducci/', '_blank', 'noopener,noreferrer')}
          >
            Lien vers le Github Page
          </button>
        </section>
        <section className="project-section bg-color-3">
          <h2>Mes comptences</h2>
          <p>tant plus orient vers le monde du frontend, je suis plus expriment sur ces technologies :</p>
          <div className="skills-container">
            <SkillCard image={htmlLogo} title="HTML5" />
            <SkillCard image={cssLogo} title="CSS3" />
            <SkillCard image={reactLogo} title="React" />
          </div>
        </section>
      </main>
    </motion.div>
  );
}

export default Portfolio;
